<template>
    <button class="btn">
        <div class="btn__wrapper">
            <font-awesome-icon :icon="[iconType, iconName]" :size="iconSize"/>
            <span class="ml-4">{{ text }}</span>
        </div>
    </button>
</template>

<script>
    export default {
        data() {
            return {
                
            }
        },
        props: {
            text: String,
            iconType: {
                type: String,
                default: 'fal'
            },
            iconName: {
                type: String,
                default: 'check'
            },
            iconSize: {
                type: String,
                default: '2x'
            }
        }
    }
</script>

<style lang="scss" scoped>
.btn__wrapper {
    @apply flex items-center py-2 px-6 border-white border bg-black select-none;
    transition: 0.2s;

    &:hover {
        @apply text-black bg-white text-black cursor-pointer;
    }

    &:active {
        @apply opacity-75;
    }
}
</style>


