<template>
    <layout-default>
        <div class="projects__content">
            <div class="projects__list">
                <div v-for="p in projects" class="project-item">
                    <a :href="'/projects/' + p.route" class="project-item__link">
                        {{ p.name }}
                    </a>
                </div>
            </div>
            <div class="projects__footer">
                <dynamic-footer>
                    <button class="py-4">
                        <span class="mr-2">DESIGN APPROACH</span>
                        <font-awesome-icon :icon="['fal', 'chevron-double-up']" />
                    </button>
                </dynamic-footer>
            </div>
        </div>
    </layout-default>
</template>

<script>
    import LayoutDefault from '../../layouts/LayoutDefault'
    import DynamicFooter from '../../components/Footer'

    export default {
        name: 'Projects',
        data() {
            return {
                projects: [
                    {
                        name: "APP CORTECLOUD",
                        route: "app-cortecloud"
                    },
                    {
                        name: "SITE CORTECLOUD",
                        route: "site-cortecloud"
                    },
                    {
                        name: "MOBILE CORTECLOUD",
                        route: "mobile-cortecloud"
                    },
                    {
                        name: "MVP PAGMOVEIS",
                        route: "pagmoveis"
                    }
                ]
            }
        },
        components: {
            LayoutDefault,
            DynamicFooter
        }    
    }
</script>

<style lang="scss" scoped>
.projects__content {
    @apply flex h-full w-full min-h-full;

    .projects__list {
        @apply flex flex-col flex-wrap justify-center content-center h-full w-full;
    }
}

.project-item {
    @apply flex border-white border m-3;
    .project-item__link {
        @apply p-24;
    }
}

.projects__footer {
    @apply absolute bottom-0 right-0 mr-16 mb-6;
}
</style>


