<template>
    <div id="main-content" class="min-h-screen min-w-screen bg-black text-white font-primary overflow-hidden">
        <!-- header -->
        <Header class="fixed w-screen z-40"/>
        <!-- end-header -->


        <!-- content -->
        <div class="absolute z-0 h-full w-full top-0 bottom-0 right-0">
            <slot/>
        </div>
        <!-- end-content -->


        <!-- social -->
        <Social class="z-40" v-if="addSocial"/>
        <!-- end-social -->


        <!-- footer -->
        <Footer class="z-20"/>
        <!-- end-footer -->
    </div>
</template>

<script>
    import Header from "../components/Header";
    import Social from "../components/Social";
    import Footer from "../components/Footer";

    export default {
        name: 'Main',
        data() {
            return {
            }
        },
        components: {
            Header,
            Social,
            Footer
        },
        props: {
            addSocial: {
                type: Boolean,
                default: true
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>


