<template>
    <layout-default>
        <div id="home" class="flex flex-col mx-auto">
            <div class="home__particles absolute h-full w-full overflow-hidden">
                <vue-particles
                    color="#dedede"
                    :particleOpacity="0.5"
                    :particlesNumber="80"
                    shapeType="polygon"
                    :particleSize="4"
                    linesColor="#D56ADE"
                    :linesWidth="1"
                    :lineLinked="true"
                    :lineOpacity="0.4"
                    :linesDistance="150"
                    :moveSpeed="4"
                    :hoverEffect="true"
                    hoverMode="grab"
                    :clickEffect="true"
                    clickMode="push"
                >
                </vue-particles>
            </div>
            <div id="home__headline" class="flex flex-col w-screen h-screen text-6xl justify-center">
                <div class="flex flex-row flex-no-wrap justify-around w-full mx-auto">
                    <div class="flex flex-row items-center">
                        <div class="flex flex-col text-right items-end mr-4">
                            <vue-typed-js :strings="['STRATEGIC', 'PRODUCT', 'EXPERIENCE']" :typeSpeed="84" :loop="true" :startDelay="800" :backDelay="800" :backSpeed="40" :showCursor="false">
                                <span class="typing"></span>
                            </vue-typed-js>
                        </div>
                        <div class="flex text-left items-center">
                            <vue-text-glitch
                                text="DESIGNER"
                                background="#000"
                                fill="#94E1E5"
                                :speed="true"
                            ></vue-text-glitch>
                        </div>
                    </div>
                </div>
            </div>
            <dynamic-footer>
                <div id="home__footer" class="flex mx-auto justify-around w-screen content-center bottom-0 absolute h-24">
                    <div class="flex justify-between w-full items-center content-center w-12 mx-16 text-center">
                        <div class="flex w-12"></div>
                        <div class="flex flex-row w-12 items-center text-center select-none">
                            <font-awesome-icon :icon="['fal', 'mouse-alt']" size="2x"/>
                            <div class="absolute ml-10"> scroll down</div>
                        </div>
                        <music-player class="flex w-12 cursor-pointer"/>
                    </div>
                </div>
            </dynamic-footer>
        </div>
    </layout-default>
</template>

<script>
import LayoutDefault from '../layouts/LayoutDefault';
import DynamicFooter from '../components/Footer';
import MusicPlayer from '../components/MusicPlayer';
import VueTextGlitch from 'vue-text-glitch';
import { VueTypedJs } from 'vue-typed-js';

export default {
    name: 'Home',
    data() {
        return {
            layout: 'div'
        }
    },
    components: {
        LayoutDefault,
        DynamicFooter,
        MusicPlayer,
        VueTextGlitch,
        VueTypedJs
    }
}
</script>

<style lang="scss" scoped>
</style>


