<template>
    <div id="music-player">
        <div class="text-white">
            <font-awesome-icon @click="active = !active" v-if="active" :icon="['fal','waveform']" size="2x"/>
            <font-awesome-icon @click="active = !active" v-if="!active" :icon="['fal','play']" size="2x"/>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                active: true
            }
        }
    }
</script>

<style lang="scss" scoped>
</style>


