<template>
    <layout-default :addSocial="false">
        <div class="about-me__content">
            <div class="about-me__headline about-me__item">
                <div class="headline__text">
                    <vue-typed-js :strings="['Im a <span class=\'text-yellow-400\'>Designer</span> that codes and loves data, based in Maring-PR, Brazil']" :typeSpeed="24" :showCursor="false" cursorChar="_">
                        <div class="typing"></div>
                    </vue-typed-js>
                </div>
                <div class="headline__image">
                    <img src="../../assets/me.png" alt="My Picture">
                </div>
            </div>
            <div class="about-me__console about-me__item">
                <div class="console__header">
                    <div class="flex flex-grow inline-flex">
                        <font-awesome-icon :icon="['fal', 'terminal']"/>
                        <h2 class="ml-4">README.md</h2>
                    </div>
                    <div class="flex">
                        <font-awesome-icon :icon="['fal', 'minus-square']" class=""/>
                    </div>
                </div>
                <div class="console__panel">
                    <h3 class="font-bold">MY EXPERIENCE</h3>
                    <br />
                    <div class="panel__content">
                        Ive been working with digital solutions since 2015, when I started as a Project Manager in a Helthcare Co-op working with stakeholders for deliver value to users. This experience made me realize how the user experience is important for the success of a solution. Since then, I decided to dedicate myself  to creating better experiences understanding and colaborating with the users and giving back to the communities that I'm a part of.
                    </div>
                </div>
            </div>
            <div class="about-me__footer">
                <dynamic-footer>
                    <dynamic-button text="DOWNLOAD RESUME" iconName="download" iconSize="lg"/>
                </dynamic-footer>
            </div>
        </div>
    </layout-default>
</template>

<script>
import LayoutDefault from "../../layouts/LayoutDefault";
import DynamicFooter from "../../components/Footer";
import DynamicButton from "../../components/Button";
import { VueTypedJs } from 'vue-typed-js';

export default {
    name: 'About',
    components: {
        LayoutDefault,
        DynamicFooter,
        DynamicButton,
        VueTypedJs
    }
}
</script>

<style lang="scss" scoped>
.about-me__content {
    @apply flex h-full w-full min-h-full;
}
.about-me__item {
    @apply flex flex-col w-2/3 items-start justify-center px-16;

    &.about-me__headline {
        img {
            @apply w-32;
        }
    }
}

.console__header {
    @apply h-8 w-full flex flex-row bg-purple-900 text-white p-3 items-center rounded-t-sm text-sm shadow;
}

.console__panel {
    @apply min-w-full bg-yellow-400 text-black font-secondary p-12 rounded-b-sm;
}

.about-me__footer {
    @apply flex w-full absolute bottom-0 justify-center z-30 mb-12
}
.headline__text {
    @apply text-4xl mb-8;
}
</style>


