<template>
    <div class="text-white flex bottom-0 left-0 my-10 mx-16 h-24 absolute">
        <div class="flex flex-col">
            <ul class="social-list">
                <li class="social-list__item">
                    <a href="mailto:louisuntitled@gmail.com" target="_blank">
                        <font-awesome-icon :icon="['fal', 'envelope']" size="lg"/>
                        <span v-if="['About'].indexOf($route.name) > -1">louisuntitled@gmail.com</span>
                        <span v-else class="text-dynamic">louisuntitled@gmail.com</span>
                    </a>
                </li>
                <li class="social-list__item">
                    <a href="https://twitter.com/luis_berns" target="_blank">
                        <font-awesome-icon :icon="['fab', 'twitter']" size="lg"/>
                        <span v-if="['About'].indexOf($route.name) > -1">@luis_berns</span>
                        <span v-else class="text-dynamic">@luis_berns</span>
                    </a>
                </li>
                <li class="social-list__item">
                    <a href="https://github.com/luisberns" target="_blank">
                        <font-awesome-icon :icon="['fab', 'github']" size="lg" />
                        <span v-if="['About'].indexOf($route.name) > -1">@luisberns</span>
                        <span v-else class="text-dynamic">@luisberns</span>
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
            }
        },
        props: {
        }
    }
</script>

<style lang="scss" scoped>
.social-list {
    @apply no-underline;

    .social-list__item {
        a {
            @apply py-2 inline-flex items-center;

            span {
                @apply font-secondary ml-4 underline;
            }

            .text-dynamic {
                @apply invisible;
            }
        }

        &:hover {
            .text-dynamic {
                @apply visible;
            }
        }
    }
}
</style>


